import findCurrency from '../../../utils/findCurrency';
import updateInputValue from './updateInputValue';

/**
 *     Select    TextField
 *
 * @param {string} newCurrency -     Select
 * @param {Function} setValue -     
 * @param {string} secondCurrencyValue -    Select
 * @param {string} currentInputValue -     
 * @param {string} currentValue -     Select
 * @param {Array} currencyData -      
 * @param {Function} setSecondInputValue -    TextField   
 * @param {Function} setSecondValue -     
 */
const handleCurrencySelectChange = (
  newCurrency,
  setValue,
  secondCurrencyValue,
  currentInputValue,
  currentValue,
  currencyData,
  setSecondInputValue,
  setSecondValue
) => {
  setValue(newCurrency);

  if (newCurrency === secondCurrencyValue) {
    if (currentInputValue?.length > 0) {
      const currency = findCurrency(currencyData, newCurrency, currentValue);
      const updateAmountInput = updateInputValue(
        newCurrency,
        currentValue,
        currency,
        currentInputValue
      );

      setSecondInputValue(updateAmountInput);
    }

    setSecondValue(currentValue);
  } else if (currentInputValue?.length > 0) {
    const currency = findCurrency(currencyData, newCurrency, currentValue);
    const updateAmountInput = updateInputValue(
      newCurrency,
      currentValue,
      currency,
      currentInputValue
    );

    setSecondInputValue(updateAmountInput);
  }
};

export default handleCurrencySelectChange;
