import findCurrency from '../../../utils/findCurrency';
import updateInputValue from './updateInputValue';

/**
 *    TextField     
 *
 * @param {string} cleanValue -   ,         
 * @param {Function} setInputValue -      TextField 
 * @param {Array} currencyData -      .
 * @param {string} currentSelectValue -     Select
 * @param {string} secondSelectValue -    Select
 * @param {Function} setSecondInputValue -     TextField   
 */
const handleCurrencyInputChange = (
  cleanValue,
  setInputValue,
  currencyData,
  secondSelectValue,
  currentSelectValue,
  setSecondInputValue
) => {
  setInputValue(cleanValue);

  if (cleanValue?.length > 0) {
    const currency = findCurrency(
      currencyData,
      secondSelectValue,
      currentSelectValue
    );
    setSecondInputValue(
      updateInputValue(
        currentSelectValue,
        secondSelectValue,
        currency,
        cleanValue
      )
    );
  } else {
    setSecondInputValue('');
  }
};

export default handleCurrencyInputChange;
